section{display:flex;flex-direction:column;align-items:center;gap:var(--margin-row);width:100%;padding:var(--margin-row) var(--margin);background:var(--background-color)}section#date{padding-bottom:calc(var(--margin-row)*2)}section#gallery{padding-top:calc(var(--margin-row)*2);color:var(--background-color);background:var(--foreground-color);overflow:hidden}section#gallery h1{position:absolute;top:-0.25em}section#map{padding-top:calc(var(--margin-row)*2)}section#map h1{position:absolute;top:-0.25em}section#map .img{overflow:hidden}section#map .img img{width:100vw;will-change:transform;transition:transform .6s var(--ease)}section#map .img img:hover{transform:scale(1.1)}